.red{
    color: #f00606
   }
   .green{
       color: #11d811
   }
   .coin-container{
    width: 95vw;
    display: flex;
    justify-content: center;
}
.coin{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom:.75rem;
    min-width: 100%;
}
.coin-symbol{
    text-transform: uppercase;
    font-size: 1.75rem;
    
}
.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coin h1{
    font-size: 2rem;
    width: 60%;
    font-weight: 400;
}
.coin-row{
    margin: .75rem;
    position: relative;
    width: 100%;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
    transition: opacity 0.2s ease, box-shadow 0.2s ease;
    opacity: 1;
    padding: 1rem;
}
.coin-marketcap{
    padding-bottom: .5rem;
}
.coin-price{
    margin-bottom: .5rem;
}
.coin-volume{
    margin-bottom: .5rem;
}
.coin-percent{
    margin-bottom: .5rem;
}

@media only screen and (min-width: 35em) {
.coin-container{
    display: flex;
    justify-content: center;
}
.coin-row{
    position: relative;
    width: 100%;
    max-width: 800px;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
    transition: opacity 0.2s ease, box-shadow 0.2s ease;
    opacity: 1;
    padding: 1rem;
}
.coin img{
    height: 40px;
    width: 40px;
    margin-right: 2rem;
}

.coin h1{
    width: 70%;
} 


.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 10%;
    padding-right: 1rem;
}

.coin-volume{
    width: 20%;
}
.coin-marketcap{
    width: 25%;
}
.coin-percent{
    width: 10%;
}
}
