@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,500&display=swap');
*{
  max-width: 100vw;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat';
  background-color: white;
  color:black;
}
.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  color: #fff
}

.coin-search{
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.coin-text{
  margin-bottom: .5rem;
  text-align: center;
}
.coin-input{
  padding-left: 1rem;
  width: 300px;
  height: 50px;
  border-radius: 15px;
  border: none;
  background-color: whitesmoke;
}
.coin-input::placeholder{
color: #000;
/* padding: 1rem; */
}
.coin-currency{
  margin-top: 1rem;
}
h1{
  font-size: 3rem;
}
h2{
  margin: 1rem;
  font-size: 2rem;
}
button{
  margin: .25rem;
  padding: .5rem;
  border-radius: 10px;
  border-color: #2551f9;
  color:white;
  background-color: #2551f9;
  min-width: 6rem;
}
